import io from './io'

export default {
    //   params: [   ]
    allTransactions (params = [0, 10]) {
        return io.post({
            api: 'transactions',
            method: 'getAllTransactions',
            params
        })
    },

    //  params: [address, page, size]  
    transactions (params) {
        return io.post({
            api: 'transactions',
            method: 'transactions',
            params
        })
    },

    //  params: []
    add (params) {
        return io.post({
            api: 'transactions',
            method: 'addTransaction',
            params
        })
    },

    // params: [id]
    transaction (params) {
        return io.post({
            api: 'transactions',
            method: 'transaction',
            params
        }) 
    }
}