import io from './io'

export default {
    //
    height () {
        return io.post({
            api: 'blocks',
            method: 'height'
        })
    },

    //  params: [height, size]
    blocks (params = [0, 10]) {
        return io.post({
            api: 'blocks',
            method: 'blocks',
            params
        })
    },

    //  params: [hash]  hashhash  hash
    block (params) {
        return io.post({
            api: 'blocks',
            method: 'block',
            params
        })
    }
}