import io from './io'

export default {
    //,  params:
    open (params) {
        return io.post({
            api: 'accounts',
            method: 'open',
            params
        })
    },

    //  params: []
    addUsername (params) {
        return io.post({
            api: 'accounts',
            method: 'addUsername',
            params
        })
    },

    //
    getFee () {
        return io.post({
            api: 'accounts',
            method: 'getUsernameFee'
        })
    },

    //  params: [address, yourAddress]
    getAccount (params) {
        return io.post({
            api: 'accounts',
            method: 'getAccount',
            params
        })
    },

    //
    getPasswordFee () {
        return io.post({
            api: 'signatures',
            method: 'getFee'
        })
    },

    //  [secret,secondSecret,publicKey,multisigAccountPublicKey]
    addSignature (params) {
        return io.post({
            api: 'signatures',
            method: 'addSignature',
            params
        })
    },

    //  params: [mnemonic]
    getPrivateKey (params) {
        return io.post({
            api: 'accounts',
            method: 'getPrivateKey',
            params
        })
    },

    //
    getMnemonic () {
        return io.post({
            api: 'accounts',
            method: 'getMnemonic'
        })
    },

    //  params: [mnemonic,lockHeight,secondSecret]
    lockHeight (params) {
        return io.post({
            api: 'accounts',
            method: 'lockHeight',
            params
        })
    },

    // [address]
    getLock (params) {
        return io.post({
            api: 'accounts',
            method: 'getAccountLock',
            params
        })
    }
}