import io from './io'

export default {
    //, params: [publicKey]
    count (params) {
        return io.post({
            api: 'contacts',
            method: 'count',
            params
        })
    },

    // params:[publicKey]
    getList (params) {
        return io.post({
            api: 'contacts',
            method: 'contacts',
            params
        }) 
    },

    //
    getFee () {
        return io.post({
            api: 'contacts',
            method: 'getFee'
        })
    },

    //  params: []
    add (params) {
        return io.post({
            api: 'contacts',
            method: 'addContact',
            params
        })
    }
}